@import "https://fonts.googleapis.com/css?family=Cardo|Pathway+Gothic+One";
@import "https://fonts.googleapis.com/css?family=Cardo|Pathway+Gothic+One";
body {
  margin: 0;
  font-family: Cardo;
}

.title-slide {
  height: 100vh;
  background-color: #1c81d380;
  position: relative;
}

.title-slide:before {
  content: "";
  width: 100%;
  height: 100%;
  mix-blend-mode: multiply;
  opacity: .5;
  background-image: url("sa-parliament.b67d084d.jpg");
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.title-slide .title-slide-content {
  width: 100%;
  text-align: center;
  color: #fff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.title-slide h1 {
  margin: 0;
  font-family: Pathway Gothic One, sans-serif;
  font-size: 6em;
}

.title-slide h2 {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-family: Pathway Gothic One, sans-serif;
  font-size: 3em;
  font-weight: normal;
}

.text-slide {
  padding: 4em 0;
}

.text-slide .text-slide-inner {
  width: 800px;
  max-width: 100%;
  text-align: justify;
  margin: 0 auto;
  font-size: 18px;
  line-height: 1.8em;
}

.text-slide .text-slide-inner p {
  width: 80%;
  color: #333;
  margin: 0 auto 2em;
}

.text-slide .text-slide-inner .pullout {
  width: 40%;
  font-family: Pathway Gothic One, sans-serif;
  font-size: 1.4em;
}

.text-slide .text-slide-inner .pullout.right {
  float: right;
  margin: 2em -20% 2em 2em;
}

.text-slide .text-slide-inner .pullout.left {
  float: left;
  margin: 2em 2em 2em -20%;
}

.text-slide .text-slide-inner .pullout img {
  width: 100%;
  display: block;
}

.timeline {
  max-width: 700px;
  flex-flow: column wrap;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.timeline__content-title {
  box-sizing: border-box;
  color: #fff;
  margin: -10px 0 0;
  padding: 0 10px;
  font-family: Pathway Gothic One, sans-serif;
  font-size: 66px;
  font-weight: normal;
  transition: all .4s;
}

.timeline__content-desc {
  box-sizing: border-box;
  color: #ffffffb3;
  margin: 0;
  font-family: Cardo;
  font-size: 15px;
  font-weight: normal;
  line-height: 25px;
}

.timeline:before {
  width: 2px;
  height: 100%;
  content: "";
  background: #ffffff12;
  margin-left: -1px;
  position: absolute;
  left: 50%;
}

@media only screen and (width <= 767px) {
  .timeline:before {
    left: 40px;
  }
}

.timeline-item {
  opacity: .3;
  filter: blur(2px);
  box-sizing: border-box;
  width: calc(50% - 40px);
  padding: 40px 0;
  transition: all .5s;
  display: flex;
  position: relative;
  transform: translateY(-80px);
}

.timeline-item:before {
  content: attr(data-text);
  letter-spacing: 3px;
  width: 100%;
  color: #ffffff80;
  text-transform: uppercase;
  opacity: 0;
  border-left: 2px solid #ffffff80;
  margin-top: -5px;
  padding-left: 15px;
  font-family: Pathway Gothic One, sans-serif;
  font-size: 24px;
  position: absolute;
  top: 70%;
  right: calc(-100% - 56px);
}

.timeline-item:nth-child(2n) {
  align-self: flex-end;
}

.timeline-item:nth-child(2n):before {
  text-align: right;
  border-left: none;
  border-right: 2px solid #ffffff80;
  padding-left: 0;
  padding-right: 15px;
  left: calc(-100% - 56px);
  right: auto;
}

.timeline-item--active {
  opacity: 1;
  filter: blur();
  transform: translateY(0);
}

.timeline-item--active:before {
  opacity: 1;
  transition: all .3s .2s;
  top: 50%;
}

.timeline-item--active .timeline__content-title {
  margin: -50px 0 20px;
}

@media only screen and (width <= 767px) {
  .timeline-item {
    width: 100%;
    padding: 0 30px 150px 80px;
    align-self: baseline !important;
  }

  .timeline-item:before {
    width: 60px;
    font-size: 18px;
    top: 50px;
    text-align: left !important;
    border: none !important;
    padding: 0 !important;
    left: 10px !important;
  }

  .timeline-item:last-child {
    padding-bottom: 40px;
  }
}

.timeline__img {
  max-width: 100%;
  box-shadow: 0 10px 15px #0006;
}

.timeline__bg {
  display: none;
}

.timeline-container {
  width: 100%;
  background-size: cover;
  background-attachment: fixed;
  padding: 80px 0;
  transition: all .3s;
  position: relative;
}

.timeline-container:before {
  width: 100%;
  height: 100%;
  content: "";
  background: #636363cc;
  position: absolute;
  top: 0;
  left: 0;
}

